<template>
    <v-container>
        <v-text-field v-model="company" label="*" class="input-group--focused"></v-text-field>
        <v-text-field v-model="name" label="*" class="input-group--focused"></v-text-field>
        <v-text-field v-model="phone" label="*" class="input-group--focused"></v-text-field>
        <v-text-field v-model="cellphone" label="" class="input-group--focused"></v-text-field>
        <v-text-field v-model="fax" label="" class="input-group--focused"></v-text-field>
        <v-text-field v-model="address" label="" class="input-group--focused"></v-text-field>
        <v-text-field v-model="email" label="e-mail*" class="input-group--focused"></v-text-field>
         <p align="left"><font size="4">:</font></p>
        <v-textarea auto-grow v-model="message" solo></v-textarea>
        <v-btn large @click="send"></v-btn>
    </v-container>
</template>

<script>
let list = ["company","name","phone","email"];


export default {
    data(){
        return {
            company:'',
            name:'',
            phone:'',
            cellphone:'',
            fax:'',
            address:'',
            email:'',
            message:''
        }
    },
    methods:{
        send(){
            for(var i of list)
            {
                console.log(this[i])
                if(this[i] === '')
                {
                    alert("")
                     document.body.scrollTop = 0
                     document.documentElement.scrollTop = 0
                    return;
                }
            }
            alert("");
           
            this.$router.push('/');
            document.body.scrollTop = 0
            document.documentElement.scrollTop = 0
        }
    }
}
</script>

<style>

</style>



